import { getMessages } from "../static-data";
import { SEND_MESSAGE } from "../constants/action-type";

export default function messages(state = getMessages(10), action) {
  switch (action.type) {
    case SEND_MESSAGE:
      const { userId, message } = action.payload;
      const userMessage = {
        [userId]: state[userId]
      };
      const number = Object.keys(state[userId]);
      userMessage[userId][number] = {
        number,
        text: message,
        is_user_msg: true
      };

      return Object.assign({}, state, userMessage);
    default:
      return state;
  }
}
